const React = require('react');



class Signup extends React.Component {
	  constructor(props) {
        super(props)
        this.state = {}
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleChange = this.handleChange.bind(this);
      }

    createUser(user) {
                client({
                method: 'POST',
                path: '/api/users',
                entity: user,
                headers: {'Content-Type': 'application/json'}
                })}

      handleChange(event) {
        const name = event.target.name
        const value = event.target.value

        this.setState({
        [name]: value
        })
        }


      handleSubmit(event) {
        var newUser = {};
        newUser['first_name'] = this.state.firstname;
        newUser['last_name'] = this.state.lastname;
        newUser['email'] = this.state.email;
        newUser['password'] = this.state.password;
        this.createUser(newUser);
        event.preventDefault();




      }




	render() {

		return (
           <div class="span3">
              <h2>Sign Up</h2>
              <form onSubmit={this.handleSubmit}>
              <label>First Name</label>
              <input type="text" name="firstname" class="span3" onChange={this.handleChange} className = "first-name"/>
              <label>Last Name</label>
              <input type="text" name="lastname" class="span3" onChange={this.handleChange} className = "last-name"/>
              <label>Email Address</label>
              <input type="email" name="email" class="span3" onChange={this.handleChange} className = "email"/>
              <label>Password</label>
              <input type="password" name="password" class="span3" onChange={this.handleChange} className = "password"/>
              <input type="submit" value="Sign up" class="btn btn-primary pull-right" className = "submit"/>
              <div class="clearfix"></div>
              </form>
           </div>
		)
	}
}

export default Signup;